.gpt3__navbar
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.gpt3__navbar-links
{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex: 1;

}

.gpt3__navbar-links_logo
{
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img
{
    width:62.56px;
    height: 16.02px;

}
.gpt3__navbar-links_container
{
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign
{
    display: flex;
    justify-content:flex-end;
    align-items: center;
}
.gpt3__navbar-links_container p,
.gpt3__navbar-sign p ,
.gpt3__navbar-menu_container p
{
    color:#fff;
     font-family: var(--font-family);
     font-weight: 500;
     line-height: 25px;
     margin: 0 1rem;
     cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button
{
    background: #FF4820;
    border-radius: 5px;
    outline: 0;
    border: none;
    font-family: 'Manrope';
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color:#fff;
    padding: 1rem 2rem;
    cursor: pointer;
}
.gpt3__navbar-menu
{
    display: none;
    margin-left:1rem;
    position: relative;
}
.gpt3__navbar-menu svg
{
    cursor: pointer;
}
.gpt3__navbar-menu_container 
{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: center;
    background-color: var(--color-footer);
    right:0px;
    position: absolute;
    top:40px;
    margin-top: 1rem;
    box-shadow:  0 0 5 rgba(0, 0, 0, 0.2);
    min-width:210px;
    border-radius: 5px;
    padding: 2rem;
}

.gpt3__navbar-menu_container  p
{
    margin :1rem 0
}

.gpt3__navbar-menu_container-links-sign
{
    display: none;
}


@media screen and (max-width:1050px){

    .gpt3__navbar-links_container
    {
        display: none;
    }
    .gpt3__navbar-menu
    {
        display: flex;
    }
    
}

@media screen  and (max-width:700px)
{

    .gpt3__navbar
    {
        padding: 2rem 4rem;
    }
}


@media screen  and (max-width:550px)
{
   

    .gpt3__navbar
    {
        padding: 2rem;
    }
    .gpt3__navbar-sign
    {
        display: none;
    }
    .gpt3__navbar-menu_container-links-sign
    {
        display: block;
    }
    
}