.gpt3__footer-links
{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: row;
    text-align: left;
    align-items: flex-start;
    
}

.gpt3__footer-links div
{
    width:250px;
    margin: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.gpt3__footer
{
    color:white;
    flex-direction: column;
    background: var(--color-footer);
    align-items: center;
    justify-content: center;
}

.gpt3__footer-heading,
.gpt3__footer-btn

{
    text-align: center;
    margin-bottom: 3rem;
    width:100%
}

.gpt3__footer-heading h1
{
    font-family:var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04em;
}

.gpt3__footer-btn 
{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    margin-bottom: 10rem;
    cursor: pointer;
}

.gpt3__footer-btn p
{
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    border: 1px solid #FFFFFF;
    padding: 1rem;
}
.gpt3__footer-links_logo
{
    display: flex;
    flex-direction: column;
}
.gpt3__footer-links_logo img
{
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;
}
.gpt3__footer-links_div h4
{
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    margin-bottom: 1rem;
}

.gpt3__footer-links_div p
{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF;
    margin: 0.5rem 0;
    cursor: pointer;
}
.gpt3__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 550px) {
    .gpt3__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .gpt3__footer-links div {
        margin: 1rem 0;
    }

    .gpt3__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .gpt3__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}