.gpt3__features
{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.gpt3__features-heading
{
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: flex-start;
    text-align: left;
    margin-right: 5rem;
    
}

.gpt3__features-heading h1
{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}
.gpt3__features-heading p
{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #FF8A71;
    margin-top: 2rem;
}

.gpt3__features-container
{
    display: flex;
    flex:1.5;
   
    flex-direction: column;
}

@media screen and (max-width:990px)
{
   .gpt3__features
   {
    flex-direction: column;
   } 

   .gpt3__features-heading
   {
    margin: 0 0 2rem 0;
   }
}

@media screen and (max-width:550px)
{
   .gpt3__features-heading h1
   {
     line-height: 38px;
     font-size: 28px;
   }
}