.gpt3__possibility
{
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-img
{
    display: flex;
    justify-content: flex-start;
    flex:1;
    align-items: center;
}

.gpt3__possibility-img img
{
    width:100%;
    height: 100%;
   
}

.gpt3__possibility-content
{
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}

.gpt3__possibility-content h4:last-child {
    color: #ff8A71;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.gpt3__possibility-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;

    margin-bottom: 2rem;
}

@media screen and (max-width:650px) {
    .gpt3__possibility
    {
        flex-direction: column;
    }
    
    .gpt3__possibility-img
    {
        margin: 1rem 0;
    }
    .gpt3__possibility-content
    {
        margin-top: 2rem;
    }
}
